:host .blockquote {
  margin: 0;
  text-align: left;
  border-left: 1px solid $primary;

  .info {
    display: flex;
    align-items: center;
    height: rem(50);
    margin-top: rem(25);
    margin-left: rem(25);
  }

  .share-style {
    display: flex;
    align-items: center;
  }

  .share-button {
    width: rem(40);
    height: rem(40);
    margin-left: 10px;
    overflow: hidden;
    background-color: $gray-400;
    border-width: 0;
    border-radius: 50%;
  }

  .icon {
    width: rem(20);
    height: rem(25);
    color: $white;
  }

  .quote {
    margin-left: rem(25);
    font-size: font-size(medium);

    @include mq('min', 'md') {
      font-size: font-size(x-large);
      letter-spacing: font-spacing(dense);
    }
  }
}

:host .author-image {
  position: relative;
  flex-shrink: 0;
  width: rem(50);
  height: rem(50);

  overflow: hidden;
  border-radius: 50%;

  .image {
    @include cover;
  }
}

:host .author-info {
  flex-grow: 1;

  dt {
    margin-left: rem(10);
    font-size: font-size(small);
    font-weight: font-weight(bold);
  }

  dd {
    margin-left: rem(10);
    font-size: font-size(small);
    color: $gray-700;
  }
}
